.main_container {
    padding: 0;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background-color: #fff;
}

.wrapper {
    width: 1100px;
    height: 100%;
    margin-top: 20px;
}

@media screen and (min-width : 700px) and (max-width: 1100) {
    .wrapper {
        width: 701px;
    }
}


@media screen and (max-width : 700px) {
    .wrapper {
        width: 100%;
    }
}

.header_container {
    width: 100%;
}

.header_container .background_image_box {
    height: 300px;
    width: 100%;
    background-color: darkgrey;
    border-radius: 20px;
    display: flex;
}
.img{
    flex: 1;
    border-radius: 20px;
}
.img_two{
    height: 100px;
    width: 100px;
    border-radius: 50%;
}
.change_background_color_container{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: -50px;
}

.change_background_color{
    height: 50px;
    width: 100px;
    background-color: grey ;
    border-radius: 40px;
    justify-content: center;
    align-items: center;
    display: flex;
    cursor: pointer;
}

.header_container .profile_picture_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: -55px;
}

@media screen and (max-width : 700px) {
    .header_container .profile_picture_container  {
        justify-content: flex-start;
        align-items:flex-start;
        margin-left: 30px;
    }
}


.change_profile_pic_container{
    display: flex;
   margin-top: -30px;
   margin-left: 50px;
}
.change_profile_pic{
    background-color: darkgrey;
    height: 30px;
    width: 30px;
    border-radius: 40px;
    cursor: pointer;
}
.header_container .profile_picture {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: blue;
}

.header_container .username {
    font-size: 20px;
    font-weight: bold;
    padding: 10px;
}

.header_container .username_short_bio {
    font-size: 14px;
    color: gray
}

.header_container .action_button_container {
    width: 100%;
    margin: auto;
    justify-content: center;
    display: flex;
}

@media screen and (max-width : 700px) {
    .header_container .action_button_container {
        justify-content: flex-start;
    }
}

.header_container .action_button_wrapper {
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header_container .action_button_wrapper .action_button {
    padding: 15px;
    border-radius: 15px;
    background-color: blue;
    color: #fff;
    cursor: pointer;
}

.chooseBGImage {
    height: 100%;
    width: 100%;
    display: none;
}
.feeds_container{
    width: auto;
    height: auto;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
   margin-left: -300px ;
}
.pics{
    height: 50px;
    width: 50px;
    background-color: darkgrey;
    border-radius: 50% ;

}
.name{
    font-size: 13px;
    margin-top: 5px;
    margin-left: 10px;
    font-weight: bold;
}
.munite{
    font-size: 10px;
    margin-top: 5px;
    margin-left: 10px
}
.feeds_name{
    flex-direction: column;
}
.feeds{
    justify-content: center;
    align-items: center;
    display: flex;
    margin-left: -400px;
    font-size: 10px;
    margin-top: 15px;
}